<template>
  <div class="flex px-6 py-4 bg-stone-200">
    <h1 class="text-2xl font-bold text-gray-700">3D Viewer</h1>
  </div>
  
  <div class="canvas-container">
    <div class="canvas-wrapper">
      <TresCanvas class="fixed-canvas" clear-color="#FFFFFF"> 
        <TresPerspectiveCamera :position="[0, 2, 5]" />
        <OrbitControls />
        <TresAmbientLight :intensity="1" />
        <TresDirectionalLight :position="[5, 5, 5]" :intensity="0.8" />
        <primitive v-if="objModel" :object="objModel" />
      </TresCanvas>
    </div>

    <!-- Âè≥‰æßÊìç‰ΩúÂå∫Âüü -->
    <div class="sidebar text-left">
      <!-- ÈÄâÊã©È¢ÑÁΩÆÊ®°Âûã -->
      <div class="preset-area">
        <label for="preset-models" class="block mb-2 font-semibold text-gray-700">ÈÄâÊã©Ê®°Âûã</label>
        <select 
          id="preset-models" 
          class="w-full p-2 border rounded-lg" 
          @change="handlePresetSelect"
        >
          <option value="">-- ÈÄâÊã©Ê®°Âûã --</option>
          <option v-for="model in presetModels" :key="model.url" :value="model.url">
            {{ model.name }}
          </option>
        </select>
      </div>

      <!-- Êñá‰ª∂‰∏ä‰º†ÊåâÈíÆ -->
      <div class="upload-area mt-4">
        <label class="block mb-2 font-semibold text-gray-700">‰∏ä‰º†Ê®°Âûã</label>
        <input 
          class="w-full p-2 border rounded-lg" 
          type="file" 
          accept=".obj" 
          @change="handleUpload" 
        />
      </div>

      <!-- Êìç‰ΩúËØ¥Êòé -->
      <div class="instructions mt-6">
        <h2 class="block font-semibold text-base text-gray-700 mb-4">Êìç‰ΩúËØ¥Êòé</h2>
        <ul class="text-gray-600">
          <li class="mb-2">üîÑ <strong>Êåâ‰ΩèÂ∑¶ÈîÆ</strong> ÊóãËΩ¨Ê®°Âûã</li>
          <li class="mb-2">ü§è <strong>Êåâ‰ΩèÂè≥ÈîÆ</strong> ÊãñÂä®Ê®°Âûã</li>
          <li class="mb-2">üîç <strong>‰ΩøÁî®ÊªöËΩÆ</strong> Áº©ÊîæÊ®°Âûã</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { markRaw } from 'vue' // ÂºïÂÖ• markRaw
import { TresCanvas } from '@tresjs/core'
import { OrbitControls } from '@tresjs/cientos'
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader'
import { MeshStandardMaterial } from 'three'
import api from '../api' // ÂØºÂÖ• API

export default {
  name: "ThreeDViewer",
  components: {
    TresCanvas,
    OrbitControls
  },
  data() {
    return {
      presetModels: [], // Â≠òÂÇ®Ê®°ÂûãÂàóË°®
      modelUrl: '', // ÂΩìÂâçÂä†ËΩΩÁöÑÊ®°Âûã URL
      objModel: null // Three.js Âä†ËΩΩÁöÑÊ®°Âûã
    }
  },
  mounted() {
    this.fetchModels()
  },
  watch: {
    modelUrl(newUrl) {
      if (!newUrl) return
      const loader = new OBJLoader()
      loader.load(newUrl, (obj) => {
        const grayMaterial = new MeshStandardMaterial({ color: 0x808080 }) // ËÆæÂÆöÁÅ∞Ëâ≤ÊùêË¥®
        obj.traverse((child) => {
          if (child.isMesh) {
            child.material = grayMaterial
          }
        })
        this.objModel = markRaw(obj) // **ÂÖ≥ÈîÆÔºö‰ΩøÁî® markRaw() ÈÅøÂÖç Vue ‰ª£ÁêÜ obj**
      })
    }
  },
  methods: {
    async fetchModels() {
      try {
        const response = await api.get_model_list()
        if (response.data.result) {
          // Ëß£ÊûêÊ®°ÂûãÂàóË°®
          this.presetModels = response.data.data.map((model) => {
            return {
              name: model, // ÂèØ‰ª•Ê†πÊçÆÈúÄË¶ÅËÆæÁΩÆÂêçÂ≠ó
              url: `/static/models/${model}` // Ê†πÊçÆÂÆûÈôÖÊñá‰ª∂Ë∑ØÂæÑË∞ÉÊï¥
            }
          })
        } else {
          console.error('Ëé∑ÂèñÊ®°ÂûãÂàóË°®Â§±Ë¥•:', response.data.message)
        }
      } catch (error) {
        console.error('API ËØ∑Ê±ÇÂ§±Ë¥•:', error)
      }
    },
    handleUpload(event) {
      const file = event.target.files[0]
      if (file) {
        this.modelUrl = URL.createObjectURL(file) // ËµãÂÄºÁªô modelUrl
      }
    },
    handlePresetSelect(event) {
      this.modelUrl = event.target.value // Êõ¥Êñ∞ modelUrl
    }
  }
}

</script>



<style>
/* È°µÈù¢Êï¥‰ΩìÂ∏ÉÂ±Ä */
.canvas-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

/* 3D ÁîªÂ∏ÉÂå∫Âüü */
.canvas-wrapper {
  width: 800px;
  height: 600px;
  position: relative;
  overflow: hidden;
  background-color: white;
}

.fixed-canvas {
  width: 100% !important;
  height: 100% !important;
  display: block;
}

/* Âè≥‰æßÊìç‰ΩúÂå∫Âüü */
.sidebar {
  width: 250px;
  padding: 20px;
  margin-left: 20px;
  background-color: #f9fafb;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
}

/* ÈÄâÊã©È¢ÑÁΩÆÊ®°Âûã */
.preset-area {
  margin-bottom: 20px;
}

/* ‰∏ä‰º†Âå∫Âüü */
.upload-area input {
  width: 100%;
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  cursor: pointer;
}

/* Êìç‰ΩúËØ¥Êòé */
.instructions {
  font-size: 14px;
}

.instructions ul {
  line-height: 1.6;
}

.instructions li {
  display: flex;
  align-items: center;
}

.instructions li strong {
  font-weight: bold;
}
</style>
